import './App.css';

function App() {
  return (
    <><h1>Hello World</h1><button id="btn">Red</button></>
  );
}

const btn = App.arguments.btn

btn.addEventListener("click", ()=>{

    if(btn.innerText === "Red"){
        btn.innerText = "Blue";
    }else{
        btn.innerText= "Red";
    }
});

export default App;
